import React from 'react';
import { Experience } from '../utils/constant';
const ExperienceItem = (props) => {
    return (<div className="experience">
        {Experience.map((item) => {
            return (<div className="resume-item">
                <p >{item.position}</p>
                <p className="experience-res-title">{item.company} | {item.location}</p>
                <p className="">{item.timeframe}</p>
            </div>)
        })}
    </div >
    )
}

export default ExperienceItem;