import React, { Component } from "react";
import {NavTitles} from "../utils/constant"
import MainMenuListItem from "../components/mainMenuListItem";
import Particle from "../components/particle";
import { headerParticleOption } from '../utils/particlejsOptions';

class header extends Component {
  state = {};
  render() {
    return (
      <header>
        <div className="header-info-area">
          <a className="info-btn" id="header-info-btn" href="#header-info-bar">
            <span className="top"></span>
            <span className="middle"></span>
            <span className="bottom"></span>
          </a>
          <div id="header-info-bar">
            <Particle className="particles-container" option={headerParticleOption} ></Particle>
            <div className="close-animatedModal close-header-info-bar">
              <i className="fas fa-times"></i>
            </div>
            <div className="modal-content">
              <nav className="menu" id="menu">
                <ul id="mainMenu">
                  {Object.entries(NavTitles).map(([key, value], index) => (
                    <MainMenuListItem key={key} name={value} />
                  ))}
                </ul>
              </nav>
            </div>
          </div>
        </div>
      </header>
    );
  }
}

export default header;
