import React from "react";
import ProjectItem from "../../components/projectItem";
import { projects } from "../../utils/constant";

function protfolio() {
  return (
    <section className="portfolio section pp-scrollable" id="portfolio">
      <div className="content-container">
        <div className="display-table">
          <div className="display-content">
            <div className="portfolio-header">
              <div className="title-small">
                <p>Involved projects</p>
              </div>
            </div>
            <div className="portfolio-content">
              <div className="container">
                <div className="row">
                  {/* <div className="col-md-12 col-sm-12">
                    <div className="portfolio-content-head">
                      <ul className="controls">
                        <li className="filter" data-filter="all">
                          ALL
                        </li>
                        <li className="filter" data-filter=".design">
                        DESIGN
                      </li>
                        <li className="filter" data-filter=".development">
                          DEVELOPMENT
                        </li>
                        <li className="filter" data-filter=".plugin">
                        PLUGIN
                      </li>
                        <li className="filter" data-filter=".photography">
                        PHOTOGRAPHY
                      </li>
                      </ul>
                    </div>
                  </div> */}
                  <div className="col-md-12 col-sm-12">
                    <div className="portfolio-content-items">
                      <div className="row">
                        {projects.map((item) => {
                          return <ProjectItem item={item}/>
                        })}
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </section>
  );
}

export default protfolio;
