import React from 'react';
 const MainMenuListItem = (props) => {
    const name = props.name.toLocaleLowerCase();
    const href = `#${name}`.replace("?","").split(" ").join("-");
    const active = props.name === "Intro" ? "active" : ""
    return (<li data-menuanchor={href} className={active}>
        <a href={href}>{props.name}</a>
    </li>)
}

export default MainMenuListItem;