import React, { Component } from "react";
import SkillItem from "../../components/skillItem";
import { LangFrames, OtherTechnologicalSkills } from "../../utils/constant";

class expertise extends Component {
    render() {
        return (
            <section className="expertise about section pp-scrollable" id="expertise">
                <div className="about-header">
                    <div className="title-small">
                        <p>Expertise</p>
                    </div>
                </div>
                <div className="content-container">
                    <div className="display-table">
                        <div className="display-content">
                            <div className="container">
                                <div className="row">
                                    <div className="col-lg-6 col-md-12">
                                        <div className="about-content">
                                            <p className="about-text">
                                                Languages and Frameworks:
                                            </p>
                                            <div className="skills skills-left">
                                                <div className="progressbar-area">
                                                <ul>
                                                    {LangFrames.map((item, index) =>
                                                        <SkillItem key={item} item={item} progressbarStatus="on" index={index} />
                                                    )}
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                    <div className="col-lg-6 col-md-12">
                                        <div className="about-content">
                                            <p className="about-text">
                                                Other Technological skills:
                                            </p>
                                            <div className="skills">
                                                <div className="progressbar-area">
                                                    <ul>
                                                {OtherTechnologicalSkills.map((item) =>
                                                        <SkillItem key={item} item={item} />
                                                    )}
                                                    </ul>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </section>
        );
    }
}

export default expertise;
