import React from 'react';
import { Education } from "../utils/constant";
const EducationItem = (props) => {
    return (<div className="education">
        {Education.map((item) => {
            return (<div className="resume-item">
                <p className="">{item.level}</p>
                <p className="experience-res-title">{item.institution} | {item.location}</p>
                <p >{item.completedYear}</p>
            </div>)
        })}
    </div>
    )
}

export default EducationItem;