import React, { Component } from "react";
import WorkingProcessItem from "../../components/workingProcessItem";
import { ScrumProcess1, ScrumProcess2 } from "../../utils/constant";

class work extends Component {
  render() {
    return (
      <section className="work section pp-scrollable" id="work">
        <div className="content-container">
          <div className="display-table">
            <div className="display-content">
              <div className="work-header">
                <div className="title-small">
                  <p>WORKING PROCESS</p>
                </div>
              </div>
              <div className="work-content">
                <div className="container">
                  <div className="row">
                    {/* <div className="col-md-6">
                      <div className="video-container">
                        <div className="video-area">
                          <a
                            href="https://www.youtube.com/watch?v=EfTUpvxEbqc"
                            className="video-play button"
                            target="_self"
                          >
                            <div className="video-icon">
                              <span>
                                <i className="fas fa-caret-right"></i>
                              </span>
                            </div>
                          </a>
                        </div>
                      </div>
                    </div> */}
                    <div className="col-md-6">
                      {ScrumProcess1.map((item, index) => (
                        <WorkingProcessItem title={item} index={index}/>
                      ))}
                      <div className="work-arrow"></div>
                    </div>
                    <div className="col-md-6">
                    <div className="work-arrow"></div>
                      {ScrumProcess2.map((item, index) => (
                        <WorkingProcessItem title={item} index={index}/>
                      ))}
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default work;
