import React, { Component } from "react";
import { Designation, FirstName, LastName, PersonalStatement } from "../../utils/constant";

class welcome extends Component {
  render() {
    return (
      <section
        className="welcome-area intro animated-text section pp-scrollable"
        id="welcome-area"
      >
        <div className="image-bg" id="welcome-image-area">
          <div className="content-container">
            <div className="overlayImg"></div>
            <div className="title-small">
              <p>INTRO</p>
            </div>
            <div className="display-table">
              <div className="display-content">
                <div className="container">
                  <div className="row">
                    <div className="col-md-12">
                      <div className="welcome-text">
                        <div className="type-wrap">
                          <div id="typed-strings">
                            <h1>
                              I'm <span>{FirstName} {LastName}</span>
                            </h1>
                            <h1>
                              I'm a <span>{Designation}</span>
                            </h1>
                          </div>
                          <span id="typed"></span>
                        </div>
                        <p>
                        {PersonalStatement}
                        </p>
                        <div className="social-icons">
                          <a href="https://www.facebook.com/aacict">
                            <i
                              className="fab fa-facebook-f"
                              aria-hidden="true"
                            ></i>
                          </a>
                          {/* <a href="#">
                            <i className="fab fa-twitter"></i>
                          </a>
                          <a href="#">
                            <i className="fab fa-google"></i>
                          </a> */}
                          {/* <a href="https://join.skype.com/invite/nYYT2ieddOS2">
                            <i className="fab fa-skype"></i>
                          </a> */}
                          <a href="https://www.linkedin.com/in/aacict/">
                            <i className="fab fa-linkedin-in"></i>
                          </a>
                        </div>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    );
  }
}

export default welcome;
